<template>
  <div class="container">
    <aside class="sidebar">
      <ul>
        <li
          v-for="file in files"
          :key="file.name"
          :class="{ active: file.name === selectedFile }"
          @click="loadFile(file.name)"
        >
          {{ file.title }}
        </li>
      </ul>
    </aside>
    <div class="content">
      <img src="@/assets/bg1.png" alt="Psychology" class="content-img" />
      <main class="markdown-body" v-html="renderedContent"></main>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { marked } from 'marked';

export default {
  name: 'PsychologyPage',
  data() {
    return {
      files: [
        { name: 'file1.md', title: '' },
        { name: 'file2.md', title: '?' },
        { name: 'file3.md', title: '' },
        { name: 'file4.md', title: '' },  
        { name: 'file5.md', title: '' },
        { name: 'file6.md', title: '' },
        { name: 'file7.md', title: '' },
        { name: 'file8.md', title: '' },
        { name: 'file9.md', title: '' },
        { name: 'file10.md', title: '' }    
      ],
      selectedFile: '',
      renderedContent: ''
    };
  },
  mounted() {
    if (this.files.length > 0) {
      this.loadFile(this.files[0].name);
    }
  },
  methods: {
    async loadFile(filename) {
      this.selectedFile = filename;
      try {
        const res = await axios.get(`/psychology/${filename}`);
        this.renderedContent = marked(res.data);
      } catch (err) {
        this.renderedContent = `<p style="color:red;">${filename}</p>`;
      }
    }
  }
};
</script>

